<template>
  <div class="pc-container pb-4">
    <Loader v-show="loader" />
    <div class="pcoded-content">
      <div v-if="mostrarOpcionAdmin">
        <CardHomeAdmin />
      </div>
      <div v-else>
        <CardHomeUser />
      </div>
      <div class="row">
        <div class="col-xl-12 col-md-12">
          <div class="card shadow rounded">
            <div class="card-header border-0">
              <h6 class="text-secondary p-0 m-0">Últimos cinco expedientes registrados en el sistema</h6>
            </div>
            <div class="card-body mx-0 px-0 mt-0 pt-0">
              <div class="table-responsive ">
                <table class="table">
                  <thead>
                    <tr>
                      <th class="pl-4">N° de Expediente</th>
                      <th>Fecha de Inicio</th>
                      <th>Demandante</th>
                      <th>Demandado</th>
                      <th>Pretensión</th>
                      <!-- <th>Materia</th> -->
                      <th>Estado</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="expediente in expedientes" :key="expediente.exp_id">
                      <td class="pl-4">
                        <router-link :to="{ name: 'expediente/detalle', params: { id: expediente.exp_id } }">
                          {{ expediente.numero }}
                        </router-link>
                      </td>
                      <td>{{ expediente.fecha_inicio }}</td>
                      <td v-if="expediente.tipo_persona == 'natural' && expediente.procesal == 'demandante'">{{
                        expediente.apellido_paterno + ' ' +
                        expediente.apellido_materno + ' ' + expediente.nombres }}
                      </td>
                      <td v-if="expediente.tipo_persona == 'juridica' && expediente.procesal == 'demandante'">{{
                        expediente.razon_social }}</td>
                      <td>UNPRG</td>
                      <!-- <td v-if="expediente.procesal == 'demandando'">Unprg</td> -->
                      <td v-if="expediente.tipo_persona == 'natural' && expediente.procesal == 'demandado'">{{
                        expediente.apellido_paterno + ' ' +
                        expediente.apellido_materno + ' ' + expediente.nombres }}
                      </td>
                      <td v-if="expediente.tipo_persona == 'juridica' && expediente.procesal == 'demandado'">{{
                        expediente.razon_social }}</td>
                      <td>{{ expediente.pretencion }}</td>
                      <!-- <td>{{ expediente.materia }}</td> -->
                      <td class="pr-5">
                        <span :class="badgeClass(expediente.estado_proceso)">
                          {{ expediente.estado_proceso }}
                        </span>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import CardHomeUser from '../components/Card/CardHomeUser.vue';
import CardHomeAdmin from '../components/Card/CardHomeAdmin.vue';
import { badgeClass } from '@/utils/utils.js';
import '@/assets/css/fileStatus.css';
import feather from 'feather-icons';
import axios from 'axios';
import Loader from '../components/Loader/LoaderModal.vue';

export default {
  name: 'dashboard',
  components: {
    CardHomeUser,
    CardHomeAdmin,
    Loader
  },
  data() {
    return {
      expedientes: [],
      loader: false
    };
  },
  computed: {
    rol() {
      return this.$store.getters.get__user.usu_rol;
    },
    mostrarOpcionAdmin() {
      return this.rol === 'ADMIN';
    },
  },
  methods: {
    listExpedients() {
      this.loader = true;
      const url = this.$store.getters.get__url + '/proceeding/take';
      const token = this.$store.getters.get__token;
      axios.post(url, {
      },
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        })
        .then((response) => {
          
          response = response.data;
          this.expedientes = response.data;
          this.loader = false;
        })
        .catch((error) => {
          console.error(error);
          this.loader = false;
        });
    },
    badgeClass
  },
  created() {

  },
  mounted() {
    this.listExpedients();
    this.$nextTick(() => {
      feather.replace();
    });
  }
}
</script>

<style scoped>
td {
  white-space: normal !important;
  word-wrap: break-word;
}

