<script>
import feather from 'feather-icons';
import axios from 'axios';
export default {
    name: 'CardHomeAdmin',
    data() {
        return {
            numberOfExpedientes: 0,
            expedientesEnProceso: 0,
            expedientesEnEjecucion: 0,
            numberOfDemandantes: 0,
            alertasPendientes: 0,
            calendario: 0,
            audienciasPendientes: 0,
        };
    },
    created() {
        
    },

    mounted() {
        this.reportes();
        this.$nextTick(() => {
            feather.replace();
        });
    },

    methods: {
        reportes: function () {
            
                const url = this.$store.getters.get__url + '/reportes/inicio';
              const token = this.$store.getters.get__token;
                axios.post(url, {
                },
                {
                    headers: {
                        Authorization: `Bearer ${token}`,
                      },
                    })
                    .then((response) => {
                      response=response.data;
                      this.numberOfExpedientes = response.exptotal;
                    this.expedientesEnProceso = response.exptramite;
                    this.expedientesEnEjecucion = response.expejecucion;
                    this.numberOfDemandantes = response.demandante;
                      })
                      .catch((error) => {
                        console.error(error);
               });
        },
    }
}
</script>
