<template>
    <div>
        <div class="row pt-2 pb-3">
            <div class="col-md-12">
                <h5>Bienvenido! {{ userName }}</h5>
                <p>A continuacion se muestra un resumen de sus actividades.</p>
            </div>
        </div>
        <div class="row">
            <div class="col-md-12">
                <div class="card shadow">
                    <div class="card-body mr-4">
                        <div class="row">
                            <div class="d-flex">
                                <span class="pc-micon text-primary px-2">
                                    <i data-feather="layers"></i>
                                </span>
                                <p class="text-dark">Tiene <span class="fw-bold">{{ numeroExpedientes }}</span>
                                    Expedientes asignados</p>
                                <a href="#" class="px-2 ml-auto">
                                    Ver mas detalles <i data-feather="arrow-right-circle" class="arrow-icon"></i>
                                </a>
                            </div>
                        </div>
                        <div class="row">
                            <div class="d-flex">
                                <span class="pc-micon text-primary px-2">
                                    <i data-feather="bell"></i>
                                </span>
                                <p class="text-dark">Tiene <span class="fw-bold">{{ numeroAlertas }}</span> Alertas
                                    pendientes.</p>
                                <a href="#" class="px-2 ml-auto">
                                    Ver mas detalles <i data-feather="arrow-right-circle" class="arrow-icon"></i>
                                </a>
                            </div>
                        </div>
                        <!-- <div class="row">
                            <div class="d-flex">
                                <span class="pc-micon text-primary px-2">
                                    <i data-feather="clock"></i>
                                </span>
                                <p class="text-dark">Tiene <span class="fw-bold">{{ numberOfExpedientes }}</span> tareas
                                    por completar.</p>
                                <a href="#" class="px-2 ml-auto">
                                    Ver mas detalles <i data-feather="arrow-right-circle" class="arrow-icon"></i>
                                </a>
                            </div>
                        </div> -->
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script >
import { mapActions } from 'vuex';
import feather from 'feather-icons';

export default {
    name: 'CardHomeUser',
    data() {
        return {
            expedientesAsinados: [],
            alertas: [],
            audiencias: [],
            numeroExpedientes: 0,
            numeroAlertas: 0,
            numeroAudiencias: 0,
        };
    },
    computed: {
        userName() {
            return this.$store.getters.get__user.name;
        },
        userRole() {
            return this.$store.getters.get__user.usu_rol;
        },
        userAbogado() {
            return this.$store.getters.get__aboid;
        }
    },
    mounted() {
        this.$nextTick(() => {
            feather.replace();
        });
    },
    methods: {
        ...mapActions(['get', 'post']),

        async listExpedientes() {
            try {
                const response = await this.post({
                    url: this.$store.getters.get__url + '/lawyer/expedientes',
                    token: this.$store.getters.get__token,
                    params: {
                        abo_id: this.userAbogado,
                    }
                });
                this.expedientesAsinados = response.data;
                this.numeroExpedientes = this.expedientesAsinados.length;
                console.log('expedientes_asignados', response);
            } catch (error) {
                console.log(error);
            }
        },

        async listAlertas() {
            try {
                const response = await this.post({
                    url: this.$store.getters.get__url + '/lawyer/alertas',
                    token: this.$store.getters.get__token,
                    params: {
                        abo_id: this.userAbogado,
                    },
                });
                if (response.state === 0 && response.data) {
                    this.alertas = response.data;
                    this.numeroAlertas = this.alertas.length;
                    console.log('alertas', response);
                } else {
                    console.error('Error en la respuesta del servidor:', response);
                }
            } catch (error) {
                console.error('Error en la solicitud:', error);
            }
        },

        async listAudiencias() {
            try {
                const response = await this.post({
                    url: this.$store.getters.get__url + '/lawyer/audiencias',
                    token: this.$store.getters.get__token,
                    params: {
                        abo_id: this.userAbogado,
                    }
                });
                this.audiencias = response.data;
                this.numeroAudiencias = this.audiencias.length;
                console.log('audiencias', response);
            } catch (error) {
                console.log(error);
            }
        },
    },

    created() {
        this.listExpedientes();
        this.listAlertas();
        // this.listAudiencias();
    },


}
</script>
<style scoped>
a {
    transition: margin-left 0.3s ease;
    /* Agrega una transición suave */
}

/* Cuando el enlace está en estado de "hover", mueve el ícono hacia la derecha */
a:hover {
    color: #6610f2;
}

.ale,
.aud {
    position: absolute;
    top: 10px;
    right: 30px;
    width: 25em !important;
    word-wrap: break-word;
    background-color: #7267EF;
    color: #fff;
    display: flex;
    padding: 16px;
    border-radius: 8px;
    transition: opacity 0.5s ease-out, transform 0.5s ease-out;
}

/* .ale.show {
    opacity: 1;
    transform: translateY(0);
} */

.ale-icon,
.aud-icon {
    margin-top: auto;
    margin-bottom: auto;
}

.ale-body,
.aud-body {
    display: flex;
    flex-direction: column;
    font-size: 12px;
}

.ale-descripcion,
.aud-descripcion {
    padding-bottom: 5px;
    margin-left: 10px;
    font-weight: 700;
}

.ale-fecha,
.aud-fecha {
    margin-left: 10px;
}

.color-azul {
    background-color: #17C666;
}

.color-naranja {
    background-color: #fd7e14;
}

.color-rojo {
    background-color: #EA4D4D;
}
</style>